  align-items: center;
  display: flex;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 1.2em;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.load-more:hover {
  background-color: #b30068;
  transform: scale(1.05);
}

.load-more.load-more-hidden {
  display: none;
}

